import React, { Component } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faUsers , faFighterJet , faTrophy} from '@fortawesome/free-solid-svg-icons'

import  './battle.less';

import Player from '../components/player';
class battle extends Component {
    render() {
        return (
            <div>
                <div className="instruction">
                    <p>Instructions</p>
                    <ul className="instruction-ul">
                        <li>
                            <figure>
                                <figcaption>Enter two Github:</figcaption>
                                <FontAwesomeIcon icon={faUsers} size="8x" color="#ffbf74"></FontAwesomeIcon>
                            </figure>
                        </li>
                        <li>
                            <figure>
                                <figcaption>Battle</figcaption>
                                <FontAwesomeIcon icon={faFighterJet} size="8x" color="#808080"></FontAwesomeIcon>
                            </figure>
                        </li>
                        <li>
                            <figure>
                                <figcaption>See the winner</figcaption>
                                <FontAwesomeIcon icon={faTrophy} size="8x" color="#f4ea2a"></FontAwesomeIcon>
                            </figure>
                        </li>
                    </ul>
                </div>
                <Player/>
            </div>
        )
    }
}
export default battle
