import React, { Component } from 'react'
import './player.less';

import axios from 'axios'

class player extends Component {
    constructor(props) {
        super(props)
        this.state = {
            infoData: [],
            isP1Disabled: true,
            isP2Disabled: true,
            valueP1: '',
            valueP2: '',
            isP1Display: true,
            isP2Display: true,
            P1: null,
            P2: null
        }
    }
    render() {
        // let isP1Disabled = this.state.isP1Disabled
        // let isP2Disabled = this.state.isP2Disabled
        // let valueP1 = this.state.valueP1
        // let valueP2 = this.state.valueP2
        let { isP1Disabled, isP2Disabled, valueP1, valueP2, isP1Display, isP2Display } = this.state
        return (
            <div className="player">
                <p>Players</p>
                <ul className="player-ul">
                    {
                        isP1Display ?
                            <li className="select-P1">
                                <h3>Player One</h3>
                                <form>
                                    <input type="text" placeholder="github username" value={valueP1} onChange={this.changeP1Evt.bind(this)} name="player1" />
                                    {/* <input type="submit" value="submit" disabled={isdisabled} /> */}
                                    <button className="btn-submit" disabled={isP1Disabled} onClick={this.subP1Evt.bind(this)}>submit</button>
                                </form>
                            </li>
                            :
                            <li className="display-P1">
                                <figure>
                                    <img src={this.state.P1.owner.avatar_url} />
                                    <figcaption>{this.state.P1.owner.login}</figcaption>
                                </figure>
                                <button className="btn-close" onClick={this.CloseP1Evt.bind(this)}>X</button>
                            </li>
                    }
                    {
                        isP2Display ?
                            <li className="select-P2" >
                                <h3>Player Two</h3>
                                <form>
                                    <input type="text" placeholder="github username" value={valueP2} onChange={this.changeP2Evt.bind(this)} name="player2" />
                                    <button className="btn-submit" disabled={isP2Disabled} onClick={this.subP2Evt.bind(this)}>submit</button>
                                </form>
                            </li>
                            :
                            <li className="display-P2">
                                <figure>
                                    <img src={this.state.P2.owner.avatar_url} />
                                    <figcaption>{this.state.P2.owner.login}</figcaption>
                                </figure>
                                <button className="btn-close" onClick={this.CloseP2Evt.bind(this)}>X</button>
                            </li>
                    }
                </ul>
                {
                    (isP1Display == false & isP2Display == false) ?
                        <button className="btn-battle" onClick={this.battleEvt.bind(this)}>Battle</button>
                        :
                        ''
                }
            </div>
        )
    }

    /*获取初始化数据*/
    async componentDidMount() {
        // if (sessionStorage.length !== 0) {
        //     var obj = JSON.parse(sessionStorage.getItem("p1"))
        //     console.log(obj);
        //     this.setState({
        //         isP1Display: false,
        //         isP2Display: false,
        //     })
        // } else {
        //     var initUrl = 'https://api.github.com/search/repositories?q=stars:%3E1&sort=stars&order=desc&type=Repositories'
        //     const res = await axios.get(initUrl)
        //     this.setState({
        //         infoData: res.data.items,
        //     })
        // }
        var initUrl = 'https://api.github.com/search/repositories?q=stars:%3E1&sort=stars&order=desc&type=Repositories'
        const res = await axios.get(initUrl)
        this.setState({
            infoData: res.data.items,
        })
    }

    /* 输入时触发的 onChange事件 */
    //P1
    changeP1Evt(e) {
        this.setState({
            valueP1: e.target.valueP1
        })
        if (e.target.value !== '') {
            this.setState({
                isP1Disabled: false
            })
        } else {
            this.setState({
                isP1Disabled: true
            })
        }
    }
    //P2
    changeP2Evt(e) {
        this.setState({
            valueP2: e.target.valueP2
        })
        if (e.target.value !== '') {
            this.setState({
                isP2Disabled: false
            })
        } else {
            this.setState({
                isP2Disabled: true
            })
        }
    }

    /*点击btn-submit时的事件*/
    subP1Evt(e) {
        e.preventDefault()
        let [player1] = this.state.infoData.filter(it => {
            return it.owner.login === e.target.previousSibling.value
        })
        sessionStorage.setItem("p1", JSON.stringify({player1}))
        this.setState({
            isP1Display: false,
            P1: player1
        })
    }
    subP2Evt(e) {
        e.preventDefault()
        let [player2] = this.state.infoData.filter(it => {
            return it.owner.login === e.target.previousSibling.value
        })
        sessionStorage.setItem("p2", JSON.stringify({player2}))
        this.setState({
            isP2Display: false,
            P2: player2
        })
    }

    /* 点击btn-close时的事件 */
    CloseP1Evt(e) {
        e.preventDefault()
        sessionStorage.removeItem("p1")
        this.setState({
            isP1Display: true
        })
    }
    CloseP2Evt(e) {
        e.preventDefault()
        sessionStorage.removeItem("p2")
        this.setState({
            isP2Display: true
        })
    }

    /* 点击btn-battle */
    battleEvt() {
        console.log(this.props);
    }
}
export default player
